import React from 'react';
import Snakbar from "react-snackbar-g";
// import Snakbar from "./snakbar";
import { Prism as SyntaxHighlighter } from 'react-syntax-highlighter'
import { darcula } from 'react-syntax-highlighter/dist/esm/styles/prism'
import white from './media/white.gif';
import dark from './media/dark.gif';
import autoHide from './media/autoHide.gif';
import {code} from './codes'

export default class App extends React.Component {
    state = {
        isOpen: false,
        snackbar: { type: 'process',title: '',message: '',qbs: Math.random() },
        isWhite: false,
    }

    snackbarUpdate = () =>{
        this.setState({
            isOpen: true,
            snackbar: {
                hide: false,
                type: 'process',
                title: 'BitPay Fetching Info',
                qbs: Math.random(),
            }
        })
        setTimeout(()=>{
            this.setState({
                snackbar: {
                    hide: false,
                    type: 'process',
                    title: 'Verifying transaction',
                    qbs: Math.random(),
                }
            })
        }, 1000)
        setTimeout(()=>{
            this.setState({
                snackbar: {
                    hide: false,
                    type: 'process',
                    title: 'Broadcasting transaction',
                    qbs: Math.random(),
                }
            })
        }, 2500)

        setTimeout(()=>{
            this.setState({ // last step close the snackbar
                isOpen: false,
                snackbar: {
                    hide: true, // set hide to true
                    title: 'Payment sent',
                    type: 'success',
                    qbs: Math.random(),
                },
                isWhite: false,
            })
        }, 4000)
    }
    show = () =>{
        this.setState({
            isOpen: true,
            snackbar: {
                hide: false,
                type: 'process',
                title: 'Snackbar never hide',
                qbs: Math.random(),
            }
        })
    }
    hide = () =>{
        this.setState({
            isOpen: false, // set to false to hide
            snackbar: {
                hide: true, // set to true to hide
                type: 'process',
                title: 'Snackbar hiding',
                qbs: Math.random(),
            }
        })
    }
    autoHide = (snackbar) =>{
        this.setState({
            isOpen: true,
            snackbar: snackbar
        })
    }
    render() {
        return (
            <div className="App">
                <header className="App-header">
                    <div style={{margin: 20}}>
                        <h1>react-snackbar-g</h1>
                        <p style={{fontSize: 14}}>Show your support by give us a star so we will improve the repository</p>
                        <span style={{color: '#292c34'}}> - </span>
                        <a className="github-button" href="https://github.com/shrroy/react-snackbar-g/issues" data-color-scheme="no-preference: light; light: light; dark: dark;" data-icon="octicon-issue-opened" data-size="large" data-show-count="true" aria-label="Issue shrroy/react-snackbar-g on GitHub">Issue</a>
                        <span style={{color: '#292c34'}}> - </span>
                        <a className="github-button" href="https://github.com/shrroy/react-snackbar-g/fork" data-color-scheme="no-preference: light; light: light; dark: dark;" data-icon="octicon-repo-forked" data-size="large" data-show-count="true" aria-label="Fork shrroy/react-snackbar-g on GitHub">Fork</a>
                        <span style={{color: '#292c34'}}> - </span>
                        <a className="github-button" href="https://github.com/shrroy/react-snackbar-g" data-color-scheme="no-preference: light; light: light; dark: dark;" data-icon="octicon-star" data-size="large" data-show-count="true" aria-label="Star shrroy/react-snackbar-g on GitHub">Star</a>
                        <span style={{color: '#292c34'}}> - </span>
                        <a className="github-button" href="https://github.com/shrroy/react-snackbar-g" data-color-scheme="no-preference: light; light: light; dark: dark;" data-icon="octicon-star" data-size="large" data-show-count="true" aria-label="Star shrroy/react-snackbar-g on GitHub">Star</a> <span />
                        <span style={{color: '#292c34'}}> - </span>
                        <a className="github-button" href="https://github.com/sponsors/shrroy" data-color-scheme="no-preference: light; light: light; dark: dark;" data-icon="octicon-heart" data-size="large" aria-label="Sponsor @shrroy on GitHub">Sponsor</a>
                    </div>

                    <div style={{marginBottom: 20}}>
                        <p style={{fontSize: 15}}>The types of react-snackbar-g, it will hide automatically with 4000ms duration</p>
                        <div style={{display: 'flex', flexWrap: 'wrap',justifyContent: 'center'}}>
                            <button className={'btn-aws iririr'} onClick={()=>this.autoHide({
                                hide: true,
                                type: 'process',
                                title: 'Processing...',
                                qbs: Math.random(),
                            })}>process</button>
                            <button className={'btn-aws iririr'} onClick={()=>this.autoHide({
                                hide: true,
                                type: 'success',
                                title: 'Process Done.',
                                message: 'Successfully performed this action.',
                                qbs: Math.random(),
                            })}>success</button>
                            <button className={'btn-aws iririr'} onClick={()=>this.autoHide({
                                hide: true,
                                type: 'error',
                                title: 'Wrong credentials',
                                message: 'The credentials you supplied were not correct',
                                qbs: Math.random(),
                            })}>error</button>
                        </div>
                    </div>
                    <hr style={{marginBottom: 40}} />
                    <div >
                        <img height="110" style={{marginRight: 10}} width={'auto'} src={autoHide} alt='snackbar'/>
                    </div>
                    <div style={{width: '70%'}}>
                        <SyntaxHighlighter language="jsx" style={darcula}>{code.zero}</SyntaxHighlighter>
                    </div>
                    <hr />

                    <div >
                        <img height="110" style={{marginRight: 10}} width={'auto'} src={dark} alt='snackbar'/>
                    </div>
                    <button className={'btn-aws'} onClick={this.snackbarUpdate}>Click me once!</button>
                    <div style={{width: '70%'}}>
                        <SyntaxHighlighter language="jsx" style={darcula}>{code.one}</SyntaxHighlighter>
                    </div>
                    <hr />
                    <div >
                        <img height="110" style={{marginRight: 10}} width={'auto'} src={white} alt='snackbar'/>
                    </div>
                    <p style={{color: '#b7b7b7'}}>Same as above code but just remove the style props like this</p>
                    <div style={{width: '70%'}}>
                        <SyntaxHighlighter language="jsx" style={darcula}>{code.two}</SyntaxHighlighter>
                    </div>
                    <hr />
                    <p style={{color: '#b7b7b7'}}>Passing hide prop to false will never hide the snackbar until setting hide to true</p>
                    <div style={{display: 'flex', flexWrap: 'wrap',justifyContent: 'center',marginBottom: 20}}>
                        <button className={'btn-aws'} style={{width: 255}} onClick={this.show}>Show never hide</button>
                        <button className={'btn-aws'} onClick={this.hide}>Hide Snackbar</button>
                    </div>
                    <div style={{width: '70%'}}>
                        <SyntaxHighlighter language="jsx" style={darcula}>{code.three}</SyntaxHighlighter>
                    </div>
                    <div style={{margin: 20}}>
                        <p><a target='_black' style={{color: '#d4d9e4',marginRight: 10,textDecoration: 'none'}} rel="noopener noreferrer" href={'https://github.com/shrroy/react-snackbar-g'} >Github react-snackbar-g</a></p>
                       <p style={{fontSize: 14}}>Show your support by give us a star so we will improve the repository</p>
                        <span style={{color: '#292c34'}}> - </span>
                        <a className="github-button" href="https://github.com/shrroy/react-snackbar-g/issues" data-color-scheme="no-preference: light; light: light; dark: dark;" data-icon="octicon-issue-opened" data-size="large" data-show-count="true" aria-label="Issue shrroy/react-snackbar-g on GitHub">Issue</a>
                        <span style={{color: '#292c34'}}> - </span>
                        <a className="github-button" href="https://github.com/shrroy/react-snackbar-g/fork" data-color-scheme="no-preference: light; light: light; dark: dark;" data-icon="octicon-repo-forked" data-size="large" data-show-count="true" aria-label="Fork shrroy/react-snackbar-g on GitHub">Fork</a>
                        <span style={{color: '#292c34'}}> - </span>
                        <a className="github-button" href="https://github.com/shrroy/react-snackbar-g" data-color-scheme="no-preference: light; light: light; dark: dark;" data-icon="octicon-star" data-size="large" data-show-count="true" aria-label="Star shrroy/react-snackbar-g on GitHub">Star</a>
                        <span style={{color: '#292c34'}}> - </span>
                        <a className="github-button" href="https://github.com/shrroy/react-snackbar-g" data-color-scheme="no-preference: light; light: light; dark: dark;" data-icon="octicon-star" data-size="large" data-show-count="true" aria-label="Star shrroy/react-snackbar-g on GitHub">Star</a> <span />
                        <span style={{color: '#292c34'}}> - </span>
                        <a className="github-button" href="https://github.com/sponsors/shrroy" data-color-scheme="no-preference: light; light: light; dark: dark;" data-icon="octicon-heart" data-size="large" aria-label="Sponsor @shrroy on GitHub">Sponsor</a>
                    </div>
                </header>
                <Snakbar
                    open={this.state.isOpen}  // *(Required)
                    onClose={()=>this.setState({isOpen: false})}  // *(Required)
                    type={this.state.snackbar.type} // *(Required)
                    qbs={this.state.snackbar.qbs} // *(Required)
                    title={this.state.snackbar.title}  // *(Required)

                    message={this.state.snackbar.message} // (optional)
                    hide={this.state.snackbar.hide} // (optional)
                    duration={4000} // (optional) default 4000
                    iconColor={'#8bc34a'} // (optional)
                    titleStyle={{fontSize: 23,color: '#fff'}} // (optional) title inline style
                    msgStyle={{color: '#fff'}} // (optional) message inline style

                    style={{background: '#2d3242'}} // (optional) icon container style
                    className={'iconContainer'} // (optional) icon container class style
                    contentStyle={{background: '#1e232f',}} // (optional) content title,message inline style
                    contentClass={'contentStyle'} // (optional) content title,message class style
                />
            </div>
        );
    }
}